export default {
	data() {
		return {
			savedFileList:[]
		}
	},
	onLoad() {
		this.getSavedFileList();
	},
	methods: {
		getSavedFileList() {
			var self = this;
			uni.getSavedFileList({
			  success: function (res) {
				  console.log("文件列表")
				  console.log(res)
			    res.fileList.forEach(item=>{
					self.savedFileList.push(item);
				})
			  }
			});
		},
		openDoc() {
			wx.openDocument({
				filePath:this.savedFileList[0].filePath,
				fileType:'pptx,',
				showMenu:true
			})
		}
	}
}
